/* Image Enhancement Styles */

/* Case Study SVG Enhancement */
.case-study-image {
  position: relative;
  transition: transform 0.3s ease;
}

.case-study-image::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, var(--image-overlay-top, rgba(0,0,0,0.1)), var(--image-overlay-bottom, rgba(0,0,0,0.6)));
  z-index: 1;
}

.image-placeholder {
  font-size: 4rem;
  position: absolute;
  z-index: 2;
  color: var(--image-placeholder, rgba(255, 255, 255, 0.7));
  mix-blend-mode: soft-light;
  filter: drop-shadow(0 0 8px var(--image-shadow, rgba(0,0,0,0.4)));
}

.case-study-card:hover .case-study-image {
  transform: scale(1.05);
}

/* Better contrast for category badges */
.case-study-category {
  text-shadow: 0px 0px 2px var(--category-shadow, rgba(0,0,0,0.5));
  font-weight: 600;
  color: var(--text-on-accent);
}

/* Enhance overlay visibility */
.case-study-overlay {
  background: linear-gradient(to top, 
    var(--overlay-1, rgba(0,0,0,0.9)) 0%, 
    var(--overlay-2, rgba(0,0,0,0.7)) 40%,
    var(--overlay-3, rgba(0,0,0,0.4)) 60%,
    transparent 100%);
}

/* Hero Section Background Enhancement */
.hero-section {
    background: var(--gradient-primary);
    position: relative;
}

/* Tech Pattern Overlay */
.hero-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
        radial-gradient(circle at 25% 25%, var(--gradient-subtle) 0%, transparent 50%),
        radial-gradient(circle at 75% 75%, var(--gradient-subtle) 0%, transparent 50%),
        linear-gradient(45deg, transparent 30%, var(--border-color) 32%, transparent 34%);
    background-size: 200px 200px, 300px 300px, 100px 100px;
    animation: techPattern 20s linear infinite;
    pointer-events: none;
}

@keyframes techPattern {
    0% { background-position: 0 0, 0 0, 0 0; }
    100% { background-position: 200px 200px, -300px -300px, 100px 100px; }
}

/* About Section Enhancement */
.about-section::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 40%;
    height: 100%;
    background: var(--gradient-subtle);
    pointer-events: none;
}

/* Contact Section Background */
.contact-section {
    background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
    position: relative;
}

/* Portfolio Card Enhancements */
.portfolio-card {
    background-image: var(--gradient-subtle);
}

/* Portfolio Grid Enhancement */
.portfolio::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 200px;
    background: linear-gradient(to top, transparent, rgba(102, 126, 234, 0.05));
    pointer-events: none;
}
