/* Global Styles */
@import './image-enhancements.css';

:root {
  /* Light Theme Colors */
  --bg-primary: #f6f8fa;        /* Very light gray base */
  --bg-secondary: #e9ecf3;      /* Light gray-blue */
  --bg-card: #ffffff;           /* White for cards */
  --bg-card-light: #f3f6fb;     /* Lighter gray for cards */
  --bg-card-lighter: #e3eaf6;   /* Even lighter blue for featured cards */
  --bg-card-lightest: #d6e4f0;  /* Lightest blue for highlighted cards */
  --text-primary: #22223b;      /* Deep navy text */
  --text-secondary: #4a4e69;    /* Muted navy text */
  --text-color: #22223b;        /* Main text color */
  --accent-primary: #5a8dee;    /* Soft blue accent */
  --accent-secondary: #7ed6df;  /* Light teal accent */
  --accent-light: #b2c7f7;      /* Lighter blue */
  --accent-lighter: #d0e6fa;    /* Lighter blue */
  --gradient-primary: linear-gradient(135deg, #5a8dee 0%, #7ed6df 100%);
  --gradient-secondary: linear-gradient(135deg, #b2c7f7 0%, #d0e6fa 100%);
  --gradient-subtle: linear-gradient(135deg, rgba(90, 141, 238, 0.08) 0%, rgba(126, 214, 223, 0.08) 100%);
  --border-color: rgba(90, 141, 238, 0.15);
  --shadow-dark: rgba(0, 0, 0, 0.08);
  --shadow-accent: rgba(90, 141, 238, 0.12);
  --text-on-accent: #fff;
  --text-placeholder: #888;
  --success-bg: rgba(34, 197, 94, 0.1);
  --success-border: rgba(34, 197, 94, 0.3);
  --success-color: #22c55e;
  --error-bg: rgba(239, 68, 68, 0.1);
  --error-border: rgba(239, 68, 68, 0.3);
  --error-color: #ef4444;
  --text-card-title: #22223b;
  --text-card-secondary: #4a4e69;
  --gradient-disabled: linear-gradient(135deg, #b0b0b0 0%, #d0d0d0 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  line-height: 1.6;
  color: var(--text-primary);
  background-color: var(--bg-primary);
}

.App {
  overflow-x: hidden;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

h1 {
  font-size: 3.5rem;
  font-weight: 800;
}

h2 {
  font-size: 2.5rem;
  font-weight: 700;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
}

p {
  margin-bottom: 1rem;
  line-height: 1.7;
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.container-fluid {
  width: 100%;
  padding: 0 20px;
}

/* Sections */
section {
  padding: 100px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
}

.section-title h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  position: relative;
}

.section-title h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.section-title p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 15px 30px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  border-radius: 50px;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  text-align: center;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 10px 30px var(--shadow-accent);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px var(--shadow-accent);
}

.btn-outline {
  background: transparent;
  color: var(--accent-primary);
  border: 2px solid var(--accent-primary);
}

.btn-outline:hover {
  background: var(--accent-primary);
  color: white;
  transform: translateY(-3px);
}

.btn-white {
  background: var(--bg-card);
  color: var(--text-primary);
  box-shadow: 0 10px 30px var(--shadow-dark);
}

.btn-white:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px var(--shadow-dark);
}

/* Cards */
.card {
  background: var(--bg-card-lighter);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 10px 40px var(--shadow-accent);
  transition: all 0.3s ease;
  border: 2px solid var(--accent-primary);
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 60px var(--shadow-accent);
  border-color: var(--accent-primary);
  background: var(--bg-card-lightest);
}

/* Grid System */
.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col-12 {
  flex: 0 0 100%;
  max-width: 100%;
  padding: 0 15px;
}

.col-6 {
  flex: 0 0 50%;
  max-width: 50%;
  padding: 0 15px;
}

.col-4 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  padding: 0 15px;
}

.col-3 {
  flex: 0 0 25%;
  max-width: 25%;
  padding: 0 15px;
}

/* Utilities */
.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.mb-4 {
  margin-bottom: 2rem;
}

.mb-5 {
  margin-bottom: 3rem;
}

.mt-4 {
  margin-top: 2rem;
}

.mt-5 {
  margin-top: 3rem;
}

/* Background Gradients */
.gradient-bg {
  background: var(--gradient-primary);
}

.gradient-text {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
  
  h1 {
    font-size: 3rem;
  }
  
  h2 {
    font-size: 2.2rem;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 720px;
  }
  
  .col-6 {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 2rem;
  }
  
  .col-4 {
    flex: 0 0 50%;
    max-width: 50%;
    margin-bottom: 2rem;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  section {
    padding: 80px 0;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 540px;
  }
  
  .col-4, .col-6 {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 2rem;
  }
  
  h1 {
    font-size: 2.2rem;
  }
  
  h2 {
    font-size: 1.8rem;
  }
  
  .btn {
    padding: 12px 25px;
    font-size: 0.9rem;
  }
  
  section {
    padding: 60px 0;
  }
}

@media (max-width: 576px) {
  .container, .container-fluid {
    padding: 0 15px;
  }
  
  h1 {
    font-size: 1.8rem;
  }
  
  .section-title h2 {
    font-size: 1.6rem;
  }
  
  .card {
    padding: 1.5rem;
  }
  
  section {
    padding: 50px 0;
  }
}