/* Contact Section */
.contact-section {
  position: relative;
  padding: 120px 0;
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
  overflow: hidden;
}

.contact-content {
  margin-top: 60px;
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: start;
}

/* Contact Form Container */
.contact-form-container {
  background: var(--bg-card-light);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 24px;
  padding: 40px;
  position: relative;
  overflow: hidden;
}

.contact-form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--gradient-primary);
}

.form-header {
  margin-bottom: 30px;
}

.form-header h3 {
  font-size: 2rem;
  color: var(--text-primary);
  margin-bottom: 10px;
}

.form-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
}

/* Form Styles */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-primary);
  font-weight: 500;
  font-size: 0.95rem;
}

.form-icon {
  color: var(--accent-primary);
  font-size: 1.1rem;
}

.form-group input,
.form-group textarea {
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 16px 18px;
  color: var(--text-primary);
  font-size: 1rem;
  transition: all 0.3s ease;
  resize: none;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--accent-primary);
  background: var(--bg-card-light);
  box-shadow: 0 0 0 3px var(--gradient-subtle);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-placeholder);
}

.form-group textarea {
  min-height: 120px;
}

/* Submit Button */
.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background: var(--gradient-primary);
  color: var(--text-on-accent);
  border: none;
  border-radius: 12px;
  padding: 18px 32px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  position: relative;
  overflow: hidden;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px var(--shadow-accent);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.submit-btn.submitting {
  background: var(--gradient-disabled);
}

.btn-icon {
  font-size: 1.2rem;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid var(--text-on-accent);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.success-message {
  background: var(--success-bg);
  border: 1px solid var(--success-border);
  color: var(--success-color);
  border-radius: 12px;
  padding: 16px;
  text-align: center;
  font-weight: 500;
  margin-top: 20px;
}

.error-message {
  background: var(--error-bg);
  border: 1px solid var(--error-border);
  color: var(--error-color);
  border-radius: 12px;
  padding: 16px;
  text-align: center;
  font-weight: 500;
  margin-top: 20px;
}

/* Contact Info */
.contact-info {
  display: flex;
  flex-direction: column;
  gap: 30px;
  background: var(--bg-card-light);
  border-radius: 24px;
  border: 1px solid var(--border-color);
  box-shadow: 0 10px 40px var(--shadow-dark);
  padding: 40px;
}

.info-header h3 {
  color: var(--text-primary);
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  position: relative;
}

.info-header h3::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.info-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto 2rem;
}

/* Contact Cards */
.contact-cards {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact-card {
  display: flex;
  align-items: center;
  gap: 20px;
  background: var(--bg-card);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 24px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.contact-card:hover {
  background: var(--bg-card-lighter);
  border-color: var(--accent-light);
}

.card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: var(--gradient-primary);
  border-radius: 12px;
  color: var(--text-on-accent);
  font-size: 1.5rem;
}

.card-content h4 {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin-bottom: 5px;
}

.card-content p,
.contact-link {
  color: var(--text-primary);
  font-size: 1rem;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-link:hover {
  color: var(--accent-primary);
}

/* Social Links */
.social-section {
  background: var(--bg-card);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 24px;
}

.social-section h4 {
  color: var(--text-primary);
  font-size: 1.2rem;
  margin-bottom: 16px;
}

.social-links {
  display: flex;
  gap: 12px;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: var(--bg-card-light);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
  font-size: 1.3rem;
}

.social-link:hover {
  background: var(--gradient-primary);
  color: var(--text-on-accent);
  transform: translateY(-2px);
}

/* CTA Box */
.cta-box {
  background: var(--gradient-subtle);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 30px;
  text-align: center;
}

.cta-box h4 {
  color: var(--text-primary);
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.cta-box p {
  color: var(--text-primary);
  margin-bottom: 20px;
}

.cta-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: var(--gradient-primary);
  color: var(--text-on-accent);
  text-decoration: none;
  border-radius: 10px;
  padding: 14px 24px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.cta-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px var(--shadow-accent);
}

/* Background Elements */
.contact-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden;
}

.bg-circle {
  position: absolute;
  border-radius: 50%;
  background: var(--gradient-subtle);
  filter: blur(40px);
}

.bg-circle-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  right: 10%;
  animation: float 6s ease-in-out infinite;
}

.bg-circle-2 {
  width: 200px;
  height: 200px;
  bottom: 20%;
  left: 10%;
  animation: float 8s ease-in-out infinite reverse;
}

.bg-gradient {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  height: 600px;
  background: var(--gradient-subtle);
  border-radius: 50%;
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(5deg); }
}

/* Contact Particles Animation */
.contact-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--accent-primary);
  opacity: 0.6;
  border-radius: 50%;
  animation: float 20s infinite linear;
}

.particle:nth-child(2n) {
  background: var(--accent-secondary);
  opacity: 0.4;
  animation-duration: 25s;
}

.particle:nth-child(3n) {
  background: var(--text-secondary);
  opacity: 0.3;
  animation-duration: 30s;
}

.particle:nth-child(4n) {
  width: 2px;
  height: 2px;
  background: var(--accent-primary);
  opacity: 0.8;
  animation-duration: 15s;
}

.particle:nth-child(5n) {
  width: 6px;
  height: 6px;
  background: var(--accent-secondary);
  opacity: 0.3;
  animation-duration: 35s;
}

@keyframes float {
  0% {
    transform: translateY(100vh) translateX(0px) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100px) translateX(100px) rotate(360deg);
    opacity: 0;
  }
}

/* Section Header */
.section-header h2 {
  color: var(--text-primary);
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  position: relative;
}

.section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.section-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto 2rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .contact-grid {
    gap: 60px;
  }
  .contact-form-container,
  .contact-info {
    padding: 30px;
    border-radius: 16px;
  }
}

@media (max-width: 900px) {
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 32px;
  }
  .contact-form-container,
  .contact-info {
    width: 100%;
    min-width: 0;
    max-width: 100vw;
    box-sizing: border-box;
  }
}

@media (max-width: 768px) {
  .contact-section {
    padding: 40px 0;
  }
  .contact-content {
    margin-top: 30px;
  }
  .contact-form-container,
  .contact-info {
    padding: 16px;
    border-radius: 14px;
    margin: 0;
    min-width: 0;
    max-width: 100vw;
    box-sizing: border-box;
  }
  .cta-box {
    padding: 16px;
    border-radius: 12px;
    margin: 0 auto;
    width: 100%;
    max-width: 100vw;
    box-sizing: border-box;
  }
  .social-links {
    flex-wrap: wrap;
    gap: 10px;
    justify-content: flex-start;
  }
  .form-row {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}

@media (max-width: 480px) {
  .contact-section {
    padding: 20px 0;
  }
  .contact-form-container,
  .contact-info {
    padding: 8px;
    border-radius: 8px;
    margin: 0;
    min-width: 0;
    max-width: 100vw;
    box-sizing: border-box;
  }
  .cta-box {
    padding: 8px;
    border-radius: 8px;
    margin: 0 auto;
    width: 100%;
    max-width: 100vw;
    box-sizing: border-box;
  }
  .contact-card {
    padding: 10px;
  }
  .form-header h3,
  .info-header h3 {
    font-size: 1.1rem;
  }
  .submit-btn {
    padding: 12px 10px;
    font-size: 0.95rem;
  }
  .form-row {
    grid-template-columns: 1fr;
    gap: 8px;
  }
}